@import '../../../styles/settings.scss';

.showcase {
    display: grid;
    justify-content: center;

    &__nav {
        display: none;
    }

    &__products {

        &__title {
            padding-bottom: 40px;
            text-align: center;
        }

        &__elements {
            display: grid;
            gap: 60px;
            justify-content: center;
        }
    
    }

}

@media (min-width: 37.5rem) {
    
    .showcase__products__elements {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 62rem) {

    .showcase {
        grid-template-columns: 1fr 3fr;
        gap: 60px;

        &__nav {
            display: block;
            margin-left: 40px;
        }

        &__products__title {
            text-align: start;
        }
    }
}

@media (min-width: 75rem) {

    .showcase__products__elements {
        grid-template-columns: repeat(3, 1fr);
        gap: 70px;
    }
}


